<template>
<Header @goscroll="gonav" />
<div class="content">
    <LandingPage />
    <Introduction />
    <Map />
    <Asking />
</div>
</template>

<script>
import Header from './components/Header.vue';
import LandingPage from './components/LandingPage.vue';
import Introduction from './components/Introduction.vue';
import Map from './components/Map.vue';
import Asking from './components/Asking.vue';
import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

export default {
    name: 'App',
    components: {
        Header,
        LandingPage,
        Introduction,
        Map,
        Asking,
    },
    methods: {
        gonav(event) {
            let docScrollY;
            if (event == "introduction") {
                docScrollY = document.querySelector('.main--introduction').offsetTop;
            } else if(event == "way-to-come") {
                docScrollY = document.querySelector('.map').offsetTop - 100;
            } else {
                docScrollY = document.querySelector('.asking').offsetTop;
            }
            window.scrollTo(0, docScrollY);
        }
    },
    mounted() {
        smoothScroll(".content");

        let introAnimation = () => {
            let introTl = gsap.timeline({})

            introTl.from('.landing__bg', {
                filter: 'blur(10px)',
                duration: 3
            }, "<")
            introTl.from('.landing__title--name', {
                opacity: 0,
                duration: 1
            }, "<2.5")
            introTl.from('.landing__title--intro-text', {
                opacity: 0,
                duration: 1
            }, "<0.5")
            introTl.to('.landing', {
                y: "-5vh",
                duration: 1,
                ease: "power2.inOut"
            }, "<1")
            introTl.to('.scroll-button', {
                yPercent: -100,
                y: '-4.9vh',
                duration: 1,
                ease: "power2.inOut"
            }, "<")
            introTl.from('header', {
                opacity: 0,
                duration: 0.5,
                ease: "power2.inOut"
            }, "<1")
            introTl.to('.arrow-line', {
                strokeDashoffset: '-30px',
                duration: 2,
                ease: "power2.inOut",
                repeat: -1,
                repeatDelay: 0.5
            }, "<0.5")
        }

        introAnimation()

        ScrollTrigger.create({
            trigger: '.introduce__text',
            start: 'top center',
            end: 'bottom top',
            animation: gsap.fromTo('.introduce__text', {
                opacity: 0,
                y: 20
            }, {
                opacity: 1,
                y: 0
            })
        })

        let introTitleTl = gsap.timeline({
            scrollTrigger: {
                trigger: '.art-hall-title',
                start: 'top+=100px center',
                end: 'bottom top',
            }
        })

        introTitleTl.fromTo('.art-hall-title', {
            opacity: 0,
            y: 20
        }, {
            opacity: 1,
            y: 0
        })
        introTitleTl.to('.art-hall-title--line', {
            width: '30px',
            duration: 0.5,
            ease: "power1.inOut"
        }, "<0.2")

        gsap.utils.toArray('.advantage li').forEach(box => {
            ScrollTrigger.create({
                trigger: box,
                start: 'center center',
                animation: gsap.fromTo(box, {
                    opacity: 0,
                    y: 20,
                    duration: 1
                }, {
                    opacity: 1,
                    y: 0,
                    duration: 1
                })
            })
        });

        let introImg = gsap.timeline({
            scrollTrigger: {
                trigger: '.introduction__image',
                start: 'top center'
            }
        })

        introImg.from('.introduction__image', {
            background: '#fff',
            duration: 0.5
        }, "<0.2")
        gsap.utils.toArray('.image--default').forEach(box => {
            introImg.from(box, {
                scale: 0,
                opacity: 0,
                duration: 1,
                ease: "power2.inOut"
            }, "<0.2")
        })

        let mapTl = gsap.timeline({
            scrollTrigger: {
                trigger: '.map',
                start: 'top center'
            }
        })

        mapTl.fromTo('.map__border', {
            width: 0,
            duration: 1,
            ease: "power1.inOut"
        }, {
            width: "100%",
            duration: 1,
            ease: "power3.inOut"
        }, "<");
        mapTl.fromTo('.map', {
            background: "#fff",
            duration: 1
        }, {
            background: "#f2f0e8",
            duration: 1
        }, "<");
        mapTl.fromTo('.map__icon', {
            opacity: 0,
            duration: 0.5
        }, {
            opacity: 1,
            duration: 0.5
        }, "<1");
        mapTl.fromTo('.map__container', {
            opacity: 0,
            duration: 1
        }, {
            opacity: 1,
            duration: 1
        }, "<0.5");

        function smoothScroll(content, viewport, smoothness) {
            content = gsap.utils.toArray(content)[0];
            smoothness = smoothness || 1;

            gsap.set(viewport || content.parentNode, {
                overflow: "hidden",
                position: "fixed",
                height: "100%",
                width: "100%",
                top: 0,
                left: 0,
                right: 0,
                bottom: 0
            });
            gsap.set(content, {
                overflow: "visible",
                width: "100%"
            });

            let getProp = gsap.getProperty(content),
                setProp = gsap.quickSetter(content, "y", "px"),
                setScroll = ScrollTrigger.getScrollFunc(window),
                removeScroll = () => content.style.overflow = "visible",
                killScrub = trigger => {
                    let scrub = trigger.getTween ? trigger.getTween() : gsap.getTweensOf(trigger.animation)[0]; // getTween() was added in 3.6.2
                    scrub && scrub.kill();
                    trigger.animation.progress(trigger.progress);
                },
                height, isProxyScrolling;

            function refreshHeight() {
                height = content.clientHeight;
                content.style.overflow = "visible"
                document.body.style.height = height + "px";
                return height - document.documentElement.clientHeight;
            }

            ScrollTrigger.addEventListener("refresh", () => {
                removeScroll();
                requestAnimationFrame(removeScroll);
            })
            ScrollTrigger.defaults({
                scroller: content
            });
            ScrollTrigger.prototype.update = p => p;

            ScrollTrigger.scrollerProxy(content, {
                scrollTop(value) {
                    if (arguments.length) {
                        isProxyScrolling = true;
                        setProp(-value);
                        setScroll(value);
                        return;
                    }
                    return -getProp("y");
                },
                scrollHeight: () => document.body.scrollHeight,
                getBoundingClientRect() {
                    return {
                        top: 0,
                        left: 0,
                        width: window.innerWidth,
                        height: window.innerHeight
                    };
                }
            });

            return ScrollTrigger.create({
                animation: gsap.fromTo(content, {
                    y: 0
                }, {
                    y: () => document.documentElement.clientHeight - height,
                    ease: "none",
                    onUpdate: ScrollTrigger.update
                }),
                scroller: window,
                invalidateOnRefresh: true,
                start: 0,
                end: refreshHeight,
                refreshPriority: -999,
                scrub: smoothness,
                onUpdate: self => {
                    if (isProxyScrolling) {
                        killScrub(self);
                        isProxyScrolling = false;
                    }
                },
                onRefresh: killScrub
            });
        }
    }
}
</script>

<style lang="scss">
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    letter-spacing: -0.05em;
}

html {
    font-size: 10px;
    font-family: $fontM;
}

html,
body {
    width: 100%;
}

li {
    list-style: none;
}
</style>
